.GradientPreview{
 width: 100vw!important;
 height: 100vh!important;
 position: relative;
}
.GradientPreview > a{
  color: #242424;
  font-weight: 900;
  padding: 15px 25px;
  position: absolute;
  top: 25px;
  left: 25px;
  text-decoration: none;
  background-color: white;
  border-radius: 5px;
  -webkit-box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  -webkit-transition: -webkit-box-shadow 200ms ease-in-out;
  transition: -webkit-box-shadow 200ms ease-in-out;
  -o-transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out, -webkit-box-shadow 200ms ease-in-out;
}
.GradientPreview > a:hover, .GradientPreview > a:focus{
  -webkit-box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
}
.GradientPreview > button{
  position: absolute;
  bottom: 25px;
  left: 25px;
  padding: 15px 25px;
  background-color: white;
  border: none;
  border-radius: 5px;
  font-family: 'Karla', sans-serif;
  font-size: 16px;
  -webkit-box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  -webkit-transition: -webkit-box-shadow 200ms ease-in-out;
  transition: -webkit-box-shadow 200ms ease-in-out;
  -o-transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out, -webkit-box-shadow 200ms ease-in-out;
  cursor: pointer;
}
.GradientPreview > button:hover, .GradientPreview > button:focus{
  -webkit-box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
}
.GradientPreview > select{
  position: absolute;
  right: 25px;
  top: 25px;
  padding: 15px 25px;
  border-radius: 5px;
  -webkit-box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  -webkit-transition: -webkit-box-shadow 200ms ease-in-out;
  transition: -webkit-box-shadow 200ms ease-in-out;
  -o-transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out, -webkit-box-shadow 200ms ease-in-out;
}
.GradientPreview > select:hover, .GradientPreview > select:focus{
  opacity: 1;
  -webkit-box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
}
.ColorCopy{
  display: -ms-flexbox;
  display: flex;
  width: auto;
  position: absolute;
  top: 25px;
  left: 50%;
  -webkit-transform: translatex(-50%);
      -ms-transform: translatex(-50%);
          transform: translatex(-50%);
}

@media screen and (max-width: 992px){
  .ColorCopy{
    -ms-flex-direction: column;
        flex-direction: column;
    left: 25px;
    top: 100px;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
  }
  .ColorDisplay{
    margin: 10px 0;
  }
}

@media screen and (max-width: 500px){
  .SingleGradient_direction{
    display: none;
  }
  .SingleGradient_copy_css{
    text-align: center;
    width: 100%;
  }
}


