.ColorDisplay{
  background-color: white;
  border: none;
  padding: 15px 25px;
  border-radius: 5px;
  -webkit-box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 5px 10px -2px rgba(110,110,110,.5);
  -webkit-transition: opacity 400ms ease-in-out, -webkit-box-shadow 200ms ease-in-out;
  transition: opacity 400ms ease-in-out, -webkit-box-shadow 200ms ease-in-out;
  -o-transition: box-shadow 200ms ease-in-out, opacity 400ms ease-in-out;
  transition: box-shadow 200ms ease-in-out, opacity 400ms ease-in-out;
  transition: box-shadow 200ms ease-in-out, opacity 400ms ease-in-out, -webkit-box-shadow 200ms ease-in-out;
  cursor: pointer;
  margin: 0 10px;
  font-family: 'Karla', sans-serif;
  text-transform: lowercase;
  font-size: 16px;
  opacity: 1;
  position: static;
}
.ColorDisplay:hover, .ColorDisplay:focus{
  -webkit-box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
  box-shadow: 0px 10px 10px -2px rgba(110,110,110,.5);
  -webkit-transition: -webkit-box-shadow 200ms ease-in-out;
  transition: -webkit-box-shadow 200ms ease-in-out;
  -o-transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out;
  transition: box-shadow 200ms ease-in-out, -webkit-box-shadow 200ms ease-in-out;
  opacity: 1;
}


